import Layout from '../components/Layout'

const Index = () => (
  <Layout>
    <h1>Hello Next.js</h1>
    <a><p>Hello Next.js</p></a>
      <style jsx>{`
        h1,
        a {
          font-family: 'Arial';
        }

        ul {
          padding: 0;
        }

        li {
          list-style: none;
          margin: 5px 0;
        }

        a {
          text-decoration: none;
          color: blue;
        }

        a:hover {
          opacity: 0.6;
        }
      `}</style>
  </Layout>
);

export default Index;